.contactInfo ul {
  position: relative;
}
.contactInfo ul li {
  list-style: none;
  margin: 10px 0;
  cursor: pointer;
}
.contactInfo ul li .icon {
  display: inline-block;
  width: 30px;
  font-size: 18px;
  color: #f9004d;
}
.contactInfo ul li span {
  color: #fff;
  font-weight: 300;
}
.text a {
  text-decoration: none;
  color: #fff;
}
.text a:hover {
  color: #f9004d;
}
.contactInfo.education li {
  margin-bottom: 15px;
}
.contactInfo.education h5 {
  color: #f9004d;
  font-weight: 500;
}
.contactInfo.education h4:nth-child(2) {
  color: #fff;
  font-weight: 500;
}
.contactInfo.education h4 {
  color: #fff;
  font-weight: 300;
}
.contactInfo.language .percent {
  position: relative;
  width: 100%;
  height: 6px;
  background: #000;
  display: block;
  margin-top: 5px;
}
.contactInfo.language .percent div {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: #f9004d;
}
.cover .right_side {
  position: relative;
  background: #fff;
  padding: 40px;
}
.aboutInfo {
  margin-bottom: 0;
}
.aboutInfo:last-child {
  margin-bottom: 0;
}
.title2 {
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 50px;
}
p {
  color: #000;
  font-weight: 500;
}
.year1 {
  color: #f9004d;
}
