@media (max-width: 768px) {
  .prodGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 500px) {
  .prodGrid {
    grid-template-columns: repeat(1, 1fr);
  }

  .prodGrid > article {
    text-align: center;
  }
}

