body {
  background: #191919;
}

.prodContainer {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: stretch;
  width: 100%;
}

.prodContainer input {
  display: flex;
  justify-content: center;
  /* flex-direction: row; */
}

.prodGrid {
  margin: 80px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  align-items: stretch;
  justify-content: center;
  /* min-height: 400px; */
}

.prodGrid > article {
  background: #fff;
  border: none;
  box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  text-align: center;
  width: 250px;
  height: 350px;
  transition: transform 0.3s;
  border: 1px solid black;
}

.prodGrid > article:hover {
  transform: translateY(5px);
  box-shadow: 2px 2px 26px 0px rgba(0, 0, 0, 0.3);
}

.prodGrid > article img {
  width: 100%;
  height: 150px;
  display: block;
  object-fit: cover;
}

.prodGrid > article h2 {
  font-size: 20px;
  font-weight: bold;
  padding: 15px;
}

