.ProdBtn {
  background-color: #f9004d;
  color: white;
  text-decoration: none;
  list-style: none;
  /* display: block; */
  text-align: center;
  border: 2px solid transparent;
  font-weight: bold;
  padding: 13px 30px;
  border-radius: 30px;
  transition: 0.4s;
  /* margin-top: 20px; */
}

.ProdBtn:hover {
  background-color: transparent;
  border: 2px solid #f9004d;
  color: #f9004d;
  cursor: pointer;
}

.proHeader {
  height: 120px;
  /* border: 2px solid #f9004d; */
}

.ank {
  text-decoration: none;
  color: #f9004d;
  cursor: pointer;
}

@media (max-width: 768px) {
  .prodGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 500px) {
  .prodGrid {
    grid-template-columns: repeat(1, 1fr);
  }

  .prodGrid > article {
    text-align: center;
  }
}
